"use client";

import { ReactNode, useEffect, useMemo, useState } from "react";

import { useAppStore } from "@/lib/store";
import { Button } from "@/components/ui/button";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { Separator } from "@/components/ui/separator";

const llmModels: Record<string, string[]> = {
  Local: ["Local Default", "Local Small", "Local Large"],
  OpenAI: ["gpt-4o-mini", "gpt-4o", "gpt-4.1"],
  Gemini: ["gemini-1.5-flash", "gemini-1.5-pro"],
  Claude: ["claude-3.5-sonnet", "claude-3-opus"],
  Grok: ["grok-2-mini", "grok-2"],
};

const whisperProviders = ["Local", "OpenAI Whisper API", "Other"] as const;
const llmProviders = ["Local", "OpenAI", "Gemini", "Claude", "Grok"] as const;
const languages = ["Auto", "de", "en", "th"] as const;

type SettingsDialogProps = {
  children: ReactNode;
};

function Section({ title, description, children }: { title: string; description?: string; children: ReactNode }) {
  return (
    <section className="space-y-3">
      <div>
        <div className="text-sm font-semibold">{title}</div>
        {description && <p className="text-xs text-muted-foreground">{description}</p>}
      </div>
      <div className="grid gap-3">{children}</div>
    </section>
  );
}

function Field({ label, children }: { label: string; children: ReactNode }) {
  return (
    <label className="grid gap-2 text-xs font-medium text-muted-foreground">
      <span>{label}</span>
      {children}
    </label>
  );
}

export function SettingsDialog({ children }: SettingsDialogProps) {
  const {
    state: { settings },
    actions: { updateSettings },
  } = useAppStore();
  const [open, setOpen] = useState(false);
  const [draft, setDraft] = useState(settings);

  useEffect(() => {
    if (open) {
      setDraft(settings);
    }
  }, [open, settings]);

  const modelOptions = useMemo(
    () => llmModels[draft.llm.provider] ?? llmModels.Local,
    [draft.llm.provider]
  );

  const handleOfflineToggle = (value: boolean) => {
    setDraft((prev) => ({
      ...prev,
      privacy: { ...prev.privacy, offline: value },
      whisper: { ...prev.whisper, provider: value ? "Local" : prev.whisper.provider },
      llm: { ...prev.llm, provider: value ? "Local" : prev.llm.provider },
    }));
  };

  return (
    <Dialog open={open} onOpenChange={setOpen}>
      <DialogTrigger asChild>{children}</DialogTrigger>
      <DialogContent aria-describedby="settings-description">
        <DialogHeader>
          <DialogTitle>Settings</DialogTitle>
          <DialogDescription id="settings-description">
            Configure providers, hotkeys, and privacy preferences.
          </DialogDescription>
        </DialogHeader>
        <div className="max-h-[70vh] space-y-6 overflow-y-auto pr-2">
          <Section title="Appearance" description="Choose a theme for the app.">
            <Field label="Theme">
              <select
                className="h-10 w-full rounded-md border border-input bg-background px-3 text-sm"
                value={draft.theme}
                onChange={(event) => setDraft((prev) => ({ ...prev, theme: event.target.value as any }))}
              >
                <option value="system">System</option>
                <option value="light">Light</option>
                <option value="dark">Dark</option>
              </select>
            </Field>
          </Section>
          <Separator />
          <Section title="Hotkey" description="Displayed shortcut for quick recording.">
            <Field label="Global Hotkey">
              <Input
                value={draft.hotkey}
                onChange={(event) => setDraft((prev) => ({ ...prev, hotkey: event.target.value }))}
              />
            </Field>
          </Section>
          <Separator />
          <Section title="Whisper (Transcription)" description="Select a transcription provider.">
            <Field label="Provider">
              <select
                className="h-10 w-full rounded-md border border-input bg-background px-3 text-sm"
                value={draft.whisper.provider}
                onChange={(event) =>
                  setDraft((prev) => ({
                    ...prev,
                    whisper: { ...prev.whisper, provider: event.target.value as any },
                  }))
                }
                disabled={draft.privacy.offline}
              >
                {whisperProviders.map((provider) => (
                  <option key={provider} value={provider}>
                    {provider}
                  </option>
                ))}
              </select>
            </Field>
            <div className="grid gap-3 md:grid-cols-2">
              <Field label="API Key">
                <Input
                  type="password"
                  placeholder="sk-..."
                  value={draft.whisper.apiKey}
                  onChange={(event) =>
                    setDraft((prev) => ({
                      ...prev,
                      whisper: { ...prev.whisper, apiKey: event.target.value },
                    }))
                  }
                  disabled={draft.whisper.provider === "Local" || draft.privacy.offline}
                />
              </Field>
              <Field label="Endpoint (optional)">
                <Input
                  placeholder="https://api.example.com"
                  value={draft.whisper.endpoint}
                  onChange={(event) =>
                    setDraft((prev) => ({
                      ...prev,
                      whisper: { ...prev.whisper, endpoint: event.target.value },
                    }))
                  }
                  disabled={draft.whisper.provider === "Local" || draft.privacy.offline}
                />
              </Field>
            </div>
            <Field label="Language">
              <select
                className="h-10 w-full rounded-md border border-input bg-background px-3 text-sm"
                value={draft.whisper.language}
                onChange={(event) =>
                  setDraft((prev) => ({
                    ...prev,
                    whisper: { ...prev.whisper, language: event.target.value as any },
                  }))
                }
              >
                {languages.map((language) => (
                  <option key={language} value={language}>
                    {language}
                  </option>
                ))}
              </select>
            </Field>
          </Section>
          <Separator />
          <Section title="LLM (Enrichment)" description="Choose a model for enrichment.">
            <Field label="Provider">
              <select
                className="h-10 w-full rounded-md border border-input bg-background px-3 text-sm"
                value={draft.llm.provider}
                onChange={(event) => {
                  const provider = event.target.value as (typeof llmProviders)[number];
                  const nextModels = llmModels[provider] ?? llmModels.Local;
                  setDraft((prev) => ({
                    ...prev,
                    llm: { ...prev.llm, provider, model: nextModels[0] ?? "" },
                  }));
                }}
                disabled={draft.privacy.offline}
              >
                {llmProviders.map((provider) => (
                  <option key={provider} value={provider}>
                    {provider}
                  </option>
                ))}
              </select>
            </Field>
            <div className="grid gap-3 md:grid-cols-2">
              <Field label="Model">
                <select
                  className="h-10 w-full rounded-md border border-input bg-background px-3 text-sm"
                  value={draft.llm.model || modelOptions[0]}
                  onChange={(event) =>
                    setDraft((prev) => ({
                      ...prev,
                      llm: { ...prev.llm, model: event.target.value },
                    }))
                  }
                  disabled={draft.privacy.offline}
                >
                  {modelOptions.map((model) => (
                    <option key={model} value={model}>
                      {model}
                    </option>
                  ))}
                </select>
              </Field>
              <Field label="API Key">
                <Input
                  type="password"
                  placeholder="sk-..."
                  value={draft.llm.apiKey}
                  onChange={(event) =>
                    setDraft((prev) => ({
                      ...prev,
                      llm: { ...prev.llm, apiKey: event.target.value },
                    }))
                  }
                  disabled={draft.llm.provider === "Local" || draft.privacy.offline}
                />
              </Field>
            </div>
            <Field label="Base URL (optional)">
              <Input
                placeholder="https://api.example.com"
                value={draft.llm.baseUrl}
                onChange={(event) =>
                  setDraft((prev) => ({
                    ...prev,
                    llm: { ...prev.llm, baseUrl: event.target.value },
                  }))
                }
                disabled={draft.llm.provider === "Local" || draft.privacy.offline}
              />
            </Field>
          </Section>
          <Separator />
          <Section title="Privacy Mode" description="Control data handling locally.">
            <label className="flex items-center justify-between rounded-xl border border-border px-4 py-3 text-sm">
              <span>Offline-first (no network)</span>
              <input
                type="checkbox"
                className="h-4 w-4 accent-primary"
                checked={draft.privacy.offline}
                onChange={(event) => handleOfflineToggle(event.target.checked)}
              />
            </label>
            <label className="flex items-center justify-between rounded-xl border border-border px-4 py-3 text-sm">
              <span>Store audio files locally</span>
              <input
                type="checkbox"
                className="h-4 w-4 accent-primary"
                checked={draft.privacy.storeAudio}
                onChange={(event) =>
                  setDraft((prev) => ({
                    ...prev,
                    privacy: { ...prev.privacy, storeAudio: event.target.checked },
                  }))
                }
              />
            </label>
          </Section>
        </div>
        <DialogFooter>
          <Button variant="ghost" onClick={() => setOpen(false)}>
            Cancel
          </Button>
          <Button
            onClick={async () => {
              const next = {
                ...draft,
                llm: { ...draft.llm, model: draft.llm.model || modelOptions[0] || "" },
              };
              await updateSettings(next);
              setOpen(false);
            }}
          >
            Save
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
}
