"use client";

import { useEffect } from "react";
import { Mic, Moon, Settings, Sun } from "lucide-react";

import { useAppStore } from "@/lib/store";
import { isEditableTarget, matchesHotkey } from "@/lib/utils";
import { Button } from "@/components/ui/button";
import { TooltipProvider, Tooltip, TooltipContent, TooltipTrigger } from "@/components/ui/tooltip";
import { Sidebar } from "@/components/Sidebar";
import { SessionView } from "@/components/SessionView";
import { SettingsDialog } from "@/components/SettingsDialog";

export function AppShell() {
  const {
    state: { settings, sessions, activeSessionId },
    actions: { toggleTheme, startRecording, stopRecording },
  } = useAppStore();

  const isDark = settings.theme === "dark";
  const activeSession = sessions.find((session) => session.id === activeSessionId) ?? null;
  const isRecording = activeSession?.status === "recording";

  useEffect(() => {
    const handler = (event: KeyboardEvent) => {
      if (isEditableTarget(event.target)) return;
      if (!matchesHotkey(event, settings.hotkey)) return;
      event.preventDefault();
      if (isRecording) {
        stopRecording();
      } else {
        void startRecording();
      }
    };

    window.addEventListener("keydown", handler);
    return () => window.removeEventListener("keydown", handler);
  }, [isRecording, settings.hotkey, startRecording, stopRecording]);

  return (
    <TooltipProvider>
      <div className="flex h-screen flex-col bg-background text-foreground">
        <header className="flex h-14 items-center justify-between border-b border-border px-6">
          <div className="flex items-center gap-2 text-sm font-semibold">
            <span className="flex h-8 w-8 items-center justify-center rounded-full bg-primary/10 text-primary">
              <Mic className="h-4 w-4" />
            </span>
            <span>Voice Intelligence</span>
          </div>
          <div className="flex items-center gap-2">
            <Tooltip>
              <TooltipTrigger asChild>
                <Button variant="ghost" size="icon" onClick={toggleTheme} aria-label="Toggle theme">
                  {isDark ? <Sun className="h-4 w-4" /> : <Moon className="h-4 w-4" />}
                </Button>
              </TooltipTrigger>
              <TooltipContent>Theme</TooltipContent>
            </Tooltip>
            <SettingsDialog>
              <Button variant="ghost" size="icon" aria-label="Settings">
                <Settings className="h-4 w-4" />
              </Button>
            </SettingsDialog>
          </div>
        </header>
        <div className="flex flex-1 overflow-hidden">
          <Sidebar />
          <main className="flex-1 overflow-y-auto bg-background">
            <SessionView />
          </main>
        </div>
      </div>
    </TooltipProvider>
  );
}
