"use client";

import { FileAudio, Mic, StopCircle } from "lucide-react";
import { useRef } from "react";

import { Mode } from "@/lib/store";
import { modeOptions } from "@/lib/mock";
import { formatDuration } from "@/lib/utils";
import { Button } from "@/components/ui/button";

const modeLabels: Record<Mode, string> = {
  smart: "Smart Notes",
  tasks: "Tasks",
  meeting: "Meeting",
  email: "Email",
};

type RecorderPanelProps = {
  status: "idle" | "recording";
  mode: Mode;
  hotkey: string;
  elapsedSeconds: number;
  level?: number;
  onModeChange: (mode: Mode) => void;
  onStart: () => void | Promise<void>;
  onStop: () => void;
  onUpload: (file: File) => void;
};

export function RecorderPanel({
  status,
  mode,
  hotkey,
  elapsedSeconds,
  level = 0,
  onModeChange,
  onStart,
  onStop,
  onUpload,
}: RecorderPanelProps) {
  const isRecording = status === "recording";
  const inputRef = useRef<HTMLInputElement | null>(null);

  return (
    <div className="flex h-full flex-col items-center justify-center px-8 py-12 text-center">
      <Button
        size="icon"
        className="h-24 w-24 rounded-full text-lg"
        onClick={() => (isRecording ? onStop() : void onStart())}
        aria-label={isRecording ? "Stop recording" : "Start recording"}
      >
        {isRecording ? <StopCircle className="h-8 w-8" /> : <Mic className="h-8 w-8" />}
      </Button>
      <div className="mt-6 text-lg font-semibold">
        {isRecording ? "Recording..." : "Ready to record"}
      </div>
      <div className="mt-1 text-sm text-muted-foreground">
        {isRecording ? `Live | ${formatDuration(elapsedSeconds)}` : `Press ${hotkey}`}
      </div>
      {isRecording && (
        <div className="mt-6 flex w-full max-w-md items-end justify-center gap-2">
          {Array.from({ length: 12 }).map((_, index) => {
            const base = 8 + level * 40;
            const variance = 0.6 + ((index % 5) / 10);
            const height = Math.max(6, Math.min(48, base * variance));
            return (
              <div
                key={index}
                className="w-2 rounded-full bg-primary/70 transition-all duration-75"
                style={{ height }}
              />
            );
          })}
        </div>
      )}
      <div className="mt-10 flex flex-wrap items-center justify-center gap-2">
        {modeOptions.map((option) => (
          <button
            key={option.value}
            onClick={() => onModeChange(option.value)}
            className={`rounded-full px-4 py-2 text-sm transition focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring disabled:cursor-not-allowed disabled:opacity-60 ${
              mode === option.value
                ? "bg-primary text-primary-foreground shadow-soft"
                : "border border-border text-muted-foreground hover:bg-muted"
            }`}
            aria-pressed={mode === option.value}
            disabled={isRecording}
          >
            {modeLabels[option.value]}
          </button>
        ))}
      </div>
      <div className="mt-6">
        <input
          ref={inputRef}
          type="file"
          accept="audio/*"
          className="hidden"
          onChange={(event) => {
            const file = event.target.files?.[0];
            if (file) {
              onUpload(file);
            }
            event.currentTarget.value = "";
          }}
        />
        <Button
          variant="secondary"
          onClick={() => inputRef.current?.click()}
          disabled={isRecording}
        >
          <FileAudio className="h-4 w-4" />
          Upload audio
        </Button>
      </div>
    </div>
  );
}
