"use client";

import { Plus, Search, Trash2 } from "lucide-react";

import { useAppStore } from "@/lib/store";
import { modeOptions } from "@/lib/mock";
import { formatDateTime } from "@/lib/utils";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Badge } from "@/components/ui/badge";
import { cn } from "@/lib/utils";

export function Sidebar() {
  const {
    state: { sessions, activeSessionId, searchQuery },
    actions: { selectSession, createEmptySession, setSearchQuery, deleteSession },
  } = useAppStore();

  const filtered = sessions.filter((session) => {
    const query = searchQuery.trim().toLowerCase();
    if (!query) return true;
    return (
      session.title.toLowerCase().includes(query) ||
      session.transcript.toLowerCase().includes(query) ||
      session.enriched.toLowerCase().includes(query)
    );
  });

  return (
    <aside className="flex w-80 flex-col border-r border-border bg-card">
      <div className="flex items-center justify-between px-5 pt-5">
        <div className="text-sm font-semibold">Your Notes</div>
        <Button variant="ghost" size="sm" onClick={createEmptySession}>
          <Plus className="h-4 w-4" />
          New
        </Button>
      </div>
      <div className="px-5 pt-4">
        <div className="relative">
          <Search className="pointer-events-none absolute left-3 top-3 h-4 w-4 text-muted-foreground" />
          <Input
            placeholder="Search"
            className="pl-9"
            aria-label="Search sessions"
            value={searchQuery}
            onChange={(event) => setSearchQuery(event.target.value)}
          />
        </div>
      </div>
      <div className="mt-4 flex-1 overflow-y-auto px-3 pb-4">
        <div className="space-y-2">
          {filtered.map((session) => {
            const modeLabel = modeOptions.find((mode) => mode.value === session.mode)?.label ?? "";
            const preview = (session.enriched || session.transcript || "No content yet.").slice(0, 80);
            const isActive = session.id === activeSessionId;
            return (
              <div
                key={session.id}
                role="button"
                tabIndex={0}
                onClick={() => selectSession(session.id)}
                onKeyDown={(event) => {
                  if (event.key === "Enter" || event.key === " ") {
                    event.preventDefault();
                    selectSession(session.id);
                  }
                }}
                className={cn(
                  "w-full rounded-2xl border border-transparent px-3 py-3 text-left transition hover:bg-muted focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring",
                  isActive && "border-border bg-muted"
                )}
                aria-current={isActive ? "page" : undefined}
              >
                <div className="flex items-center justify-between gap-2">
                  <div className="text-sm font-medium">
                    {session.title.trim() ? session.title : "Untitled"}
                  </div>
                  <div className="flex items-center gap-2">
                    <Badge variant="outline">{modeLabel}</Badge>
                    <Button
                      variant="ghost"
                      size="icon"
                      className="h-7 w-7"
                      onClick={(event) => {
                        event.stopPropagation();
                        deleteSession(session.id);
                      }}
                      aria-label="Delete note"
                    >
                      <Trash2 className="h-4 w-4" />
                    </Button>
                  </div>
                </div>
                <div className="mt-1 text-xs text-muted-foreground">
                  {formatDateTime(session.createdAt)}
                </div>
                <div className="mt-2 truncate text-xs text-muted-foreground">{preview}</div>
              </div>
            );
          })}
          {!filtered.length && (
            <div className="rounded-2xl border border-dashed border-border p-4 text-xs text-muted-foreground">
              Keine Sessions gefunden.
            </div>
          )}
        </div>
      </div>
    </aside>
  );
}
